"use client";

import { useState } from "react";
import { useQuery, useMutation } from "convex/react";
import { api } from "../../../convex/_generated/api";
import { Id } from "../../../convex/_generated/dataModel";
import Navigation from "@/components/Navigation";
import { useToast } from "@/components/Toast";

export default function CampaignsPage() {
    const campaigns = useQuery(api.campaigns.list);
    const templates = useQuery(api.templates.list);
    const senders = useQuery(api.senders.list);
    const createCampaign = useMutation(api.campaigns.create);
    const startCampaign = useMutation(api.campaigns.start);
    const pauseCampaign = useMutation(api.campaigns.pause);
    const toast = useToast();

    const [showModal, setShowModal] = useState(false);
    const [name, setName] = useState("");
    const [templateId, setTemplateId] = useState<Id<"templates"> | "">("");
    const [senderId, setSenderId] = useState<Id<"senders"> | "">("");
    const [isSubmitting, setIsSubmitting] = useState(false);

    const handleCreate = async () => {
        if (!name.trim() || !templateId || !senderId) return;
        setIsSubmitting(true);
        try {
            await createCampaign({
                name: name.trim(),
                templateId: templateId as Id<"templates">,
                senderId: senderId as Id<"senders">,
            });
            setShowModal(false);
            resetForm();
            toast.success("Campaign created! Add contacts then start sending.");
        } catch (error) {
            console.error(error);
            toast.error("Failed to create campaign");
        } finally {
            setIsSubmitting(false);
        }
    };

    const resetForm = () => {
        setName("");
        setTemplateId("");
        setSenderId("");
    };

    const handleStart = async (id: Id<"campaigns">) => {
        try {
            await startCampaign({ id });
            toast.success("Campaign started! Emails are being sent.");
        } catch {
            toast.error("Failed to start campaign");
        }
    };

    const handlePause = async (id: Id<"campaigns">) => {
        try {
            await pauseCampaign({ id });
            toast.info("Campaign paused");
        } catch {
            toast.error("Failed to pause campaign");
        }
    };

    return (
        <Navigation>
            <div>
                {/* Header */}
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "flex-start", marginBottom: 32 }}>
                    <div>
                        <h1 className="page-title">Campaigns</h1>
                        <p className="page-subtitle">Create and manage email campaigns</p>
                    </div>
                    <button className="btn btn-primary" onClick={() => setShowModal(true)}>
                        <span>+</span> New Campaign
                    </button>
                </div>

                {/* Campaigns List */}
                {!campaigns ? (
                    <div className="loading">
                        <div className="spinner" />
                    </div>
                ) : campaigns.length === 0 ? (
                    <div className="card">
                        <div className="empty-state">
                            <div className="empty-icon">üìß</div>
                            <h3 className="empty-title">No campaigns yet</h3>
                            <p className="empty-text">Create your first campaign to start sending emails</p>
                            <button className="btn btn-primary" onClick={() => setShowModal(true)}>
                                Create Campaign
                            </button>
                        </div>
                    </div>
                ) : (
                    <div className="table-container">
                        <div className="table-scroll">
                            <table>
                                <thead>
                                    <tr>
                                        <th>Campaign</th>
                                        <th>Template</th>
                                        <th>Status</th>
                                        <th>Recipients</th>
                                        <th>Progress</th>
                                        <th>Actions</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {campaigns.map((campaign: { _id: Id<"campaigns">; name: string; templateName: string; status: string; totalRecipients: number; sentCount: number; failedCount: number }) => {
                                        const progress = campaign.totalRecipients > 0
                                            ? Math.round(((campaign.sentCount + campaign.failedCount) / campaign.totalRecipients) * 100)
                                            : 0;
                                        return (
                                            <tr key={campaign._id}>
                                                <td style={{ fontWeight: 600 }}>{campaign.name}</td>
                                                <td style={{ color: "var(--text-secondary)" }}>{campaign.templateName || "‚Äî"}</td>
                                                <td>
                                                    <span
                                                        className={`badge ${campaign.status === "running"
                                                            ? "badge-success"
                                                            : campaign.status === "completed"
                                                                ? "badge-info"
                                                                : campaign.status === "paused"
                                                                    ? "badge-warning"
                                                                    : "badge-neutral"
                                                            }`}
                                                    >
                                                        {campaign.status}
                                                    </span>
                                                </td>
                                                <td>
                                                    <div style={{ display: "flex", flexDirection: "column" }}>
                                                        <span>{campaign.totalRecipients}</span>
                                                        <span style={{ fontSize: 12, color: "var(--text-muted)" }}>
                                                            {campaign.sentCount} sent, {campaign.failedCount} failed
                                                        </span>
                                                    </div>
                                                </td>
                                                <td style={{ minWidth: 150 }}>
                                                    <div style={{ display: "flex", alignItems: "center", gap: 8 }}>
                                                        <div className="progress-container" style={{ flex: 1 }}>
                                                            <div className="progress-bar" style={{ width: `${progress}%` }} />
                                                        </div>
                                                        <span style={{ fontSize: 12, color: "var(--text-muted)", minWidth: 36 }}>{progress}%</span>
                                                    </div>
                                                </td>
                                                <td>
                                                    <div style={{ display: "flex", gap: 8 }}>
                                                        {campaign.status === "draft" && (
                                                            <button className="btn btn-success btn-sm" onClick={() => handleStart(campaign._id)}>
                                                                Start
                                                            </button>
                                                        )}
                                                        {campaign.status === "running" && (
                                                            <button className="btn btn-secondary btn-sm" onClick={() => handlePause(campaign._id)}>
                                                                Pause
                                                            </button>
                                                        )}
                                                        {campaign.status === "paused" && (
                                                            <button className="btn btn-success btn-sm" onClick={() => handleStart(campaign._id)}>
                                                                Resume
                                                            </button>
                                                        )}
                                                    </div>
                                                </td>
                                            </tr>
                                        );
                                    })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                )}

                {/* Modal */}
                {showModal && (
                    <div className="modal-overlay" onClick={(e) => e.target === e.currentTarget && setShowModal(false)}>
                        <div className="modal">
                            <div className="modal-header">
                                <h2 className="modal-title">Create Campaign</h2>
                                <button className="modal-close" onClick={() => setShowModal(false)}>√ó</button>
                            </div>

                            <div className="modal-body">
                                <div className="form-group">
                                    <label className="form-label">Campaign Name *</label>
                                    <input
                                        type="text"
                                        className="form-input"
                                        placeholder="e.g., January Newsletter"
                                        value={name}
                                        onChange={(e) => setName(e.target.value)}
                                    />
                                </div>

                                <div className="form-group">
                                    <label className="form-label">Template *</label>
                                    <select
                                        className="form-select"
                                        value={templateId}
                                        onChange={(e) => setTemplateId(e.target.value as Id<"templates">)}
                                    >
                                        <option value="">Select a template...</option>
                                        {templates?.map((t: { _id: string; name: string }) => (
                                            <option key={t._id} value={t._id}>{t.name}</option>
                                        ))}
                                    </select>
                                    {!templates?.length && (
                                        <p className="form-hint" style={{ color: "var(--accent-warning)" }}>
                                            No templates available. Create one first.
                                        </p>
                                    )}
                                </div>

                                <div className="form-group">
                                    <label className="form-label">Sender *</label>
                                    <select
                                        className="form-select"
                                        value={senderId}
                                        onChange={(e) => setSenderId(e.target.value as Id<"senders">)}
                                    >
                                        <option value="">Select a sender...</option>
                                        {senders?.map((s: { _id: string; name: string; fromEmail: string }) => (
                                            <option key={s._id} value={s._id}>{s.name} ({s.fromEmail})</option>
                                        ))}
                                    </select>
                                    {!senders?.length && (
                                        <p className="form-hint" style={{ color: "var(--accent-warning)" }}>
                                            No senders configured. Set up SMTP first.
                                        </p>
                                    )}
                                </div>

                                <div
                                    style={{
                                        background: "var(--bg-tertiary)",
                                        borderRadius: "var(--radius-md)",
                                        padding: 16,
                                        marginTop: 8,
                                    }}
                                >
                                    <div style={{ fontSize: 13, color: "var(--text-secondary)", marginBottom: 8 }}>
                                        üìß Campaign will send to all <strong>active</strong> contacts
                                    </div>
                                    <div style={{ fontSize: 13, color: "var(--text-muted)" }}>
                                        Bounced, unsubscribed, and complained contacts are automatically excluded.
                                    </div>
                                </div>
                            </div>

                            <div className="modal-footer">
                                <button className="btn btn-secondary" onClick={() => setShowModal(false)}>
                                    Cancel
                                </button>
                                <button
                                    className="btn btn-primary"
                                    onClick={handleCreate}
                                    disabled={!name.trim() || !templateId || !senderId || isSubmitting}
                                >
                                    {isSubmitting ? "Creating..." : "Create Campaign"}
                                </button>
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </Navigation>
    );
}
